* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.About {
  margin-bottom: 8%;
}
.About > .Heading {
  width: 18%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 2%;
}
.About > .Heading > p {
  color: #c8fff4;
  font-size: 2rem;
  font-weight: bold;
  font-family: "Raleway", sans-serif;
  letter-spacing: 1.5px;
}
.About > .About_display {
  width: 80%;
  margin: auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.About > .About_display > div {
  width: 50%;
  /* border: 1px solid white; */
}
.About > .About_display > div:first-child {
  width: 30%;
}
.About > .About_display > div > p {
  color: white;
  text-align: justify;
  font-size: large;
  letter-spacing: 1.2px;
  line-height: 28px;
  word-spacing: 0.8px;
}
.About > .About_display > div > img {
  height: 500px;
  width: 100%;
  object-fit: cover;
  cursor: pointer;
}
@media all and (max-width: 1280px) {
  .About > .Heading {
    width: 19%;
    margin-bottom: 3%;
  }
  .About > .Heading > p {
    font-size: 1.8rem;
  }
  .About > .Heading > img {
    width: 90px;
    height: 90px;
  }
  .About > .About_display > div:first-child {
    width: 32%;
  }
  .About > .About_display > div > p {
    color: white;
    text-align: justify;
    font-size: 1rem;
    letter-spacing: 1px;
    line-height: 26px;
    word-spacing: 0.5px;
  }
  .About > .About_display > div > img {
    height: 400px;
    width: 100%;
    object-fit: scale-down;
  }
}
@media all and (max-width: 1024px) {
  .About > .Heading {
    width: 21%;
    margin-bottom: 3%;
  }
  .About > .Heading > p {
    font-size: 1.6rem;
  }
  .About > .Heading > img {
    width: 80px;
    height: 80px;
  }
  .About > .About_display > div:first-child {
    width: 35%;
  }
  .About > .About_display > div > p {
    color: white;
    text-align: justify;
    font-size: 0.9rem;
    letter-spacing: 1px;
    line-height: 23px;
    word-spacing: 0.6px;
  }
  .About > .About_display > div > img {
    height: 350px;
    width: 100%;
    object-fit: scale-down;
    cursor: pointer;
  }
}
@media all and (max-width: 912px) {
  .About > .Heading {
    width: 22%;
    margin-bottom: 2.5%;
  }
  .About > .Heading > p {
    font-size: 1.4rem;
  }
  .About > .Heading > img {
    width: 70px;
    height: 70px;
    image-rendering: crisp-edges;
  }
  .About > .About_display > div:first-child {
    width: 38%;
  }
  .About > .About_display > div > p {
    font-size: 0.85rem;
    letter-spacing: 1px;
    line-height: 23px;
    word-spacing: 0.6px;
  }
  .About > .About_display > div > img {
    height: 350px;
    width: 100%;
    object-fit: scale-down;
  }
}
@media all and (max-width: 820px) {
  .About > .Heading {
    width: 23%;
    margin-bottom: 2%;
  }
  .About > .Heading > p {
    font-size: 1.3rem;
  }
  .About > .Heading > img {
    width: 60px;
    height: 60px;
    image-rendering: crisp-edges;
  }
  .About > .About_display > div:first-child {
    width: 40%;
  }
  .About > .About_display > div > p {
    font-size: 0.8rem;
    letter-spacing: 1px;
    line-height: 21px;
    word-spacing: 0.6px;
  }
  .About > .About_display > div > img {
    height: 300px;
    width: 100%;
    object-fit: scale-down;
  }
}
