* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.Introduction {
  width: 80%;
  margin: auto;
  margin-bottom: 8%;
  color: var(--yellow-color);
  display: flex;
  justify-content: space-between;
}
.Introduction > .Text {
  width: 50%;
}
.Introduction > .Text > h1 {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 4rem;
  letter-spacing: 1px;
  animation: glow 2s linear infinite;
}
.Introduction > .Text > .Designation {
  background-color: white;
  color: rgb(101, 101, 101);
  border-radius: 1px;
  font-size: 1.8rem;
  padding: 2px 0px;
}
.Introduction > .Text > p {
  margin-top: 5%;
  color: rgb(255, 191, 127);
  letter-spacing: 0.5px;
  font-size: medium;
}
.Introduction > .Text > .Explore {
  margin-top: 5%;
  display: flex;
  align-items: center;
}
.Introduction > .Text > .Explore > a {
  margin-right: 5%;
}
.Introduction > .Text > .Explore > a > svg {
  opacity: 0.7;
  transition: opacity 0.2s ease-in;
}
.Introduction > .Text > .Explore > a > svg:hover {
  opacity: 1;
}
.Introduction > .Text > .Explore > a > button {
  background-color: #005457;
  color: white;
  padding: 10px 20px;
  font-size: large;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 1px;
  transition: all 0.2s ease-in;
  cursor: pointer;
  border-radius: 4px;
  border: none;
}
.Introduction > .Text > .Explore > a > button:hover {
  background-color: #016568;
}
.Introduction > .Profile > img {
  width: 100%;
  height: 380px;
  object-fit: contain;
}
@keyframes glow {
  50% {
    opacity: 0.7;
    color: rgb(255, 173, 73);
  }
}
@media all and (max-width: 1280px) {
  .Introduction > .Text > h1 {
    font-size: 3.5rem;
  }
  .Introduction > .Text > .Designation {
    font-size: 1.5rem;
    padding: 1.5px 0px;
  }
  .Introduction > .Text > p {
    margin-top: 4%;
    text-align: justify;
    font-size: 0.9rem;
  }
  .Introduction > .Text > .Explore > a {
    margin-right: 6%;
  }
  .Introduction > .Text > .Explore {
    margin-top: 6%;
  }
  .Introduction > .Text > .Explore > a > button {
    padding: 8px 15px;
    font-size: medium;
  }
  .Introduction > .Profile > img {
    width: 100%;
    height: 320px;
    object-fit: cover;
  }
}
@media all and (max-width: 1024px) {
  .Introduction > .Text > h1 {
    font-size: 3rem;
  }
  .Introduction > .Text > .Designation {
    font-size: 1.2rem;
    padding: 1.5px 0px;
  }
  .Introduction > .Text > p {
    margin-top: 5%;
    text-align: justify;
    font-size: 0.8rem;
  }
  .Introduction > .Text > .Explore > a {
    margin-right: 7%;
  }
  .Introduction > .Text > .Explore {
    margin-top: 7%;
  }
  .Introduction > .Text > .Explore > a > svg {
    width: 30px;
    height: 30px;
    object-fit: cover;
  }
  .Introduction > .Text > .Explore > a > button {
    padding: 6px 10px;
    font-size: small;
  }
  .Introduction > .Profile > img {
    width: 100%;
    height: 280px;
    object-fit: cover;
  }
}
@media all and (max-width: 912px) {
  .Introduction > .Text > h1 {
    font-size: 2.5rem;
  }
  .Introduction > .Text > .Designation {
    font-size: 1rem;
    padding: 1.5px 0px;
  }
  .Introduction > .Text > p {
    margin-top: 5%;
    text-align: justify;
    font-size: 0.7rem;
  }
  .Introduction > .Text > .Explore > a {
    margin-right: 8%;
  }
  .Introduction > .Text > .Explore {
    margin-top: 8%;
  }
  .Introduction > .Text > .Explore > a > svg {
    width: 27px;
    height: 27px;
    object-fit: cover;
  }
  .Introduction > .Text > .Explore > a > button {
    padding: 5px 10px;
    font-size: 0.8rem;
  }
  .Introduction > .Profile > img {
    width: 100%;
    height: 240px;
    object-fit: cover;
  }
}
@media all and (max-width: 820px) {
  .Introduction > .Text > h1 {
    font-size: 2rem;
  }
  .Introduction > .Text > p {
    margin-top: 4%;
    text-align: justify;
    font-size: 0.6rem;
  }
  .Introduction > .Text > .Explore > a {
    margin-right: 8%;
  }
  .Introduction > .Text > .Explore {
    margin-top: 8%;
  }
  .Introduction > .Text > .Explore > a > svg {
    width: 25px;
    height: 25px;
    object-fit: cover;
  }
  .Introduction > .Text > .Explore > a > button {
    padding: 5px 10px;
    font-size: 0.75rem;
  }
  .Introduction > .Profile > img {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
}
