* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.Projects {
  margin-bottom: 8%;
}
.Projects > h1 {
  color: #c8fff4;
  font-size: 2rem;
  font-weight: bold;
  font-family: "Raleway", sans-serif;
  letter-spacing: 1.5px;
  text-align: center;
  margin-bottom: 2%;
}
.Projects > .Projects_display {
  width: 80%;
  margin: auto;
  display: grid;
  grid-template-columns: 1fr;
}
.Projects > .Projects_display > div {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.Projects_display > div > div {
  width: 50%;
}
.Projects_display > div > .Text {
  padding: 5% 5%;
  background-color: rgb(40, 40, 40);
  transition: 0.8s;
}
.Projects_display > div > div > img {
  width: 100%;
  object-fit: cover;
  transition: 0.35s;
}
.Projects_display > div > div > .Heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1%;
}
.Projects_display > div > div > .Heading > h2 {
  font-size: 1.5rem;
  letter-spacing: 0.5px;
  cursor: pointer;
}
.Projects_display > div > div > .Heading > h2 > a {
  text-decoration: none;
  color: #017862;
  transition: color 0.2s ease-in;
}
.Projects_display > div > div > .Heading > h2 > a:hover {
  color: #01a587;
}
.Projects_display > div > div > .Heading > a > svg {
  cursor: pointer;
  opacity: 0.7;
  transition: opacity 0.2s ease-in;
}
.Projects_display > div > div > .Heading > a > svg:hover {
  opacity: 1;
}
.Projects_display > div > div > p {
  color: white;
  text-align: justify;
  font-size: 1rem;
  letter-spacing: 0.6px;
  line-height: 25px;
}
@media all and (max-width: 1280px) {
  .Projects > h1 {
    font-size: 1.8rem;
  }
  .Projects_display > div > .Text {
    padding: 5% 5%;
  }
  .Projects_display > div > div > .Heading > h2 {
    font-size: 1.4rem;
    letter-spacing: 0.5px;
  }
  .Projects_display > div > div > .Heading > a > svg {
    width: 28px;
    height: 28px;
  }
  .Projects_display > div > div > p {
    font-size: 0.9rem;
    letter-spacing: 0.6px;
    line-height: 24px;
  }
}
@media all and (max-width: 1024px) {
  .Projects > h1 {
    font-size: 1.6rem;
  }
  .Projects_display > div > .Text {
    padding: 5%;
  }
  .Projects_display > div > div > .Heading > h2 {
    font-size: 1.3rem;
    letter-spacing: 0.5px;
  }
  .Projects_display > div > div > .Heading > a > svg {
    width: 26px;
    height: 26px;
  }
  .Projects_display > div > div > p {
    font-size: 0.85rem;
    letter-spacing: 0.5px;
    line-height: 23px;
  }
}
@media all and (max-width: 912px) {
  .Projects > h1 {
    font-size: 1.4rem;
    margin-bottom: 2.5%;
  }
  .Projects_display > div > .Text {
    padding: 5%;
  }
  .Projects_display > div > div > .Heading > h2 {
    font-size: 1.2rem;
    letter-spacing: 0.5px;
  }
  .Projects_display > div > div > .Heading > a > svg {
    width: 24px;
    height: 24px;
  }
  .Projects_display > div > div > p {
    font-size: 0.8rem;
    letter-spacing: 0.45px;
    line-height: 22px;
  }
}
@media all and (max-width: 820px) {
  .Projects > h1 {
    font-size: 1.3rem;
    margin-bottom: 2%;
  }
  .Projects_display > div > .Text {
    padding: 5%;
  }
  .Projects_display > div > div > .Heading > h2 {
    font-size: 1.1rem;
    letter-spacing: 0.4px;
  }
  .Projects_display > div > div > .Heading > a > svg {
    width: 22px;
    height: 22px;
  }
  .Projects_display > div > div > p {
    font-size: 0.75rem;
    letter-spacing: 0.4px;
    line-height: 21px;
  }
}
