* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.Contact {
  display: flex;
  align-items: center;
  padding: 2%;
}
.Contact > .Details {
  width: 75%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.Contact > .Details > div {
  color: white;
  letter-spacing: 0.8px;
}
.Contact > .Details > div > a > svg {
  margin-right: 10px;
}
.Contact > .Details > div > a {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
  font-size: medium;
  color: white;
  opacity: 0.8;
  transition: 0.35s;
}
.Contact > .Details > div > a:hover {
  opacity: 1;
}
.Love {
  padding-bottom: 10px;
  background-color: rgb(31, 31, 31);
}
.Love > p {
  color: white;
  text-align: center;
  letter-spacing: 1px;
}
.Love > p > img {
  width: 50px;
  height: 40px;
  object-fit: cover;
}
@media all and (max-width: 1280px) {
  .Contact > h1 {
    font-size: 1.8rem;
  }
  .Contact > .Details > div > svg {
    margin-right: 4%;
    width: 30px;
    height: 30px;
  }
}
@media all and (max-width: 1024px) {
  .Contact {
    padding: 0%;
  }
  .Contact > .Details {
    width: 75%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .Contact > .Details > div > a > svg {
    margin-right: 8px;
    width: 22px;
    height: 22px;
  }
  .Contact > .Details > div > a {
    font-size: 0.9rem;
  }
  .Contact > .Details > div {
    margin-bottom: 4%;
  }
  .Contact > .Details > div > svg {
    margin-right: 5%;
    width: 28px;
    height: 28px;
  }
  .Love {
    padding-bottom: 0px;
  }
}
@media all and (max-width: 912px) {
  .Contact {
    padding: 0%;
  }
  .Contact > .Details {
    width: 75%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .Contact > .Details > div > a > svg {
    margin-right: 10px;
    width: 22px;
    height: 22px;
  }
  .Contact > .Details > div > a {
    font-size: 0.8rem;
  }
  .Contact > .Details > div {
    margin-bottom: 4%;
  }
  .Contact > .Details > div > svg {
    margin-right: 5%;
    width: 26px;
    height: 26px;
  }
  .Love > p > img {
    width: 40px;
    height: 30px;
    object-fit: cover;
  }
  .Love > p {
    font-size: 0.7rem;
  }
}
@media all and (max-width: 820px) {
  .Contact > .Details > div > a > svg {
    margin-right: 10px;
    width: 21px;
    height: 21px;
  }
  .Contact > .Details > div > a {
    font-size: 0.75rem;
  }
  .Contact > .Details > div {
    margin-bottom: 4%;
  }
  .Contact > .Details > div > svg {
    margin-right: 5%;
    width: 24px;
    height: 24px;
  }
  .Love > p > img {
    width: 35px;
    height: 25px;
    object-fit: cover;
  }
  .Love > p {
    font-size: 0.7rem;
  }
}
