* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.Stack {
  margin-bottom: 8%;
}
.Stack > h1 {
  color: #c8fff4;
  font-size: 2rem;
  font-weight: bold;
  font-family: "Raleway", sans-serif;
  letter-spacing: 1.5px;
  text-align: center;
  margin-bottom: 2%;
}
.Stack > .Display_div {
  width: 80%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 30px 25px;
}
.Stack > .Display_div > div {
  text-align: center;
  color: white;
  cursor: pointer;
  background-color: rgb(37, 37, 37);
  padding: 40px 20px;
  animation: glow 2s ease-in-out;
}
@keyframes glow {
  50% {
    background-color: rgb(255, 255, 255);
  }
}
.Stack > .Display_div > div > p {
  font-size: large;
  letter-spacing: 0.8px;
}
.Stack > .Display_div > div > img {
  width: 70px;
  width: 70px;
  object-fit: cover;
  margin-top: 5%;
  margin-bottom: 20px;
  opacity: 0.8;
  transition: all 0.2s ease-in;
}
.Stack > .Display_div > div > img:hover {
  opacity: 1;
  width: 80px;
  width: 80px;
}
@media all and (max-width: 1280px) {
  .Stack > h1 {
    font-size: 1.8rem;
  }
  .Stack > .Display_div {
    gap: 25px 20px;
  }
  .Stack > .Display_div > div {
    padding: 25px 15px;
  }
  .Stack > .Display_div > div > p {
    font-size: medium;
    letter-spacing: 0.8px;
  }
  .Stack > .Display_div > div > img {
    width: 60px;
    width: 60px;
    object-fit: cover;
    margin-bottom: 18px;
    opacity: 0.8;
    transition: all 0.2s ease-in;
  }
  .Stack > .Display_div > div > img:hover {
    opacity: 1;
    width: 70px;
    width: 70px;
  }
}
@media all and (max-width: 1024px) {
  .Stack > h1 {
    font-size: 1.6rem;
  }
  .Stack > .Display_div {
    gap: 20px 15px;
  }
  .Stack > .Display_div > div {
    padding: 20px 10px;
  }
  .Stack > .Display_div > div > p {
    font-size: small;
    letter-spacing: 0.8px;
  }
  .Stack > .Display_div > div > img {
    width: 50px;
    width: 50px;
    object-fit: cover;
    margin-bottom: 18px;
    opacity: 0.8;
    transition: all 0.2s ease-in;
  }
  .Stack > .Display_div > div > img:hover {
    opacity: 1;
    width: 60px;
    width: 60px;
  }
}
@media all and (max-width: 912px) {
  .Stack > h1 {
    font-size: 1.4rem;
    margin-bottom: 2.5%;
  }
  .Stack > .Display_div {
    gap: 14px 12px;
  }
  .Stack > .Display_div > div {
    padding: 20px 10px;
  }
  .Stack > .Display_div > div > p {
    font-size: 0.8rem;
    letter-spacing: 0.8px;
  }
  .Stack > .Display_div > div > img {
    width: 40px;
    width: 40px;
    object-fit: cover;
    margin-bottom: 14px;
    opacity: 0.8;
    transition: all 0.2s ease-in;
  }
  .Stack > .Display_div > div > img:hover {
    opacity: 1;
    width: 50px;
    width: 50px;
  }
}
@media all and (max-width: 820px) {
  .Stack > h1 {
    font-size: 1.3rem;
    margin-bottom: 2%;
  }
  .Stack > .Display_div {
    gap: 12px 10px;
  }
  .Stack > .Display_div > div {
    padding: 15px 10px;
  }
  .Stack > .Display_div > div > p {
    font-size: 0.65rem;
    letter-spacing: 0.8px;
  }
  .Stack > .Display_div > div > img {
    width: 35px;
    width: 35px;
    object-fit: cover;
    margin-bottom: 10px;
    opacity: 0.8;
    transition: all 0.2s ease-in;
  }
  .Stack > .Display_div > div > img:hover {
    opacity: 1;
    width: 45px;
    width: 45px;
  }
}