    justify-content: space-between;
    align-items: flex-end;
  }
  .calendar_div {
    background-color: #0d1117;
    margin-top: 3%;
    text-align: center;
    padding: 0.5%;
    padding-bottom: 3%;
  }
  .calendar_div > p {
    color: #03dac5;
    font-size: 1rem;
    letter-spacing: 0.5px;
    font-weight: 550;
    margin-bottom: 3%;
    margin-top: 2%;
  }
  .color-fg-muted {
    color: #03dac5;
    margin-top: 1%;
  }
  .calendar_div > .calendar {
    width: 95%;
    margin: auto;
  }
}
