@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap");
:root {
  --yellow-color: rgb(253, 192, 78);
}
.Navbar {
  text-align: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2% 5%;
  background-color: #005457;
  color: white;
  margin-bottom: 5%;
  position: sticky;
  top: 0;
  z-index: 10000;
}
.Navbar > div:last-child {
  width: 40%;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.Navbar > div > h1 > span,
.Navbar > div > p {
  cursor: pointer;
}
.Navbar > div > h1 {
  letter-spacing: 1.5px;
  font-size: 2rem;
  font-family: "Raleway", sans-serif;
}
.Navbar > div > h1 > span {
  color: white;
  transition: color 0.15s ease-in;
}

.Navbar > div > a > p {
  font-size: 1.2rem;
  font-weight: 450;
  color: white;
  transition: color 0.15s ease-in;
  letter-spacing: 0.5px;
}
.Navbar > div > a {
  text-decoration: none;
}
.Navbar > div > a > p:hover,
.Navbar > div > a > button:hover {
  color: #00c4b4;
}
.Navbar > div > h1 > span:hover {
  color: rgb(192, 255, 255);
}
.Navbar > div > a > button {
  color: white;
  font-size: 1.2rem;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 1px;
  transition: all 0.2s ease-in;
  cursor: pointer;
  border-radius: 4px;
  border: none;
  background-color: #005457;
}

@media all and (max-width: 1280px) {
  .Navbar > div:last-child {
    width: 45%;
  }
  .Navbar > div > h1 {
    font-size: 1.9rem;
  }
  .Navbar > div > a > p,
  .Navbar > div > a > button {
    font-size: 1.1rem;
  }
}
@media all and (max-width: 1024px) {
  .Navbar > div:last-child {
    width: 48%;
  }
  .Navbar > div > h1 {
    font-size: 1.8rem;
  }
  .Navbar > div > a > p,
  .Navbar > div > a > button {
    font-size: 1rem;
  }
}
@media all and (max-width: 912px) {
  .Navbar > div:last-child {
    width: 51%;
  }
  .Navbar > div > h1 {
    font-size: 1.7rem;
  }
  .Navbar > div > a > p,
  .Navbar > div > a > button {
    font-size: 0.9rem;
  }
}
@media all and (max-width: 820px) {
  .Navbar > div:last-child {
    width: 54%;
  }
  .Navbar > div > h1 {
    font-size: 1.6rem;
  }
  .Navbar > div > a > p,
  .Navbar > div > a > button {
    font-size: 0.85rem;
  }
}
